@import url(https://fonts.googleapis.com/css?family=Istok+Web&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  background-color: black;
}

.App {
  color:white;
  font-family: Istok Web;
}

.App-link {
  color: #61dafb;
}
.movie {
  margin: 20px;
}

.movie :hover {
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
.movie-container{
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  justify-items: center;
  padding: auto;
  margin: 20px auto;
}

.log-in-form{
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: auto;
}

.log-in{
  background-color: black;
  border-color: red;
  color: red;
  height: 30px;
  margin: 7px;
  width: 175px;
  font-size: 20px;
}

.log-in-btn {
  background-color: black;
  border-color: red;
  border-radius: 20px;
  color: red;
  font-size: 1.5rem;
  height: 35px;
  margin: 10px;
  width: 150px;
}

::-webkit-input-placeholder {
  color: red;
  font-size: 20px;
}

::-moz-placeholder {
  color: red;
  font-size: 20px;
}

:-ms-input-placeholder {
  color: red;
  font-size: 20px;
}

::-ms-input-placeholder {
  color: red;
  font-size: 20px;
}

::placeholder {
  color: red;
  font-size: 20px;
}

.log-in-bar{
  background-color: red;
}

.error {
  text-align: center;
  padding: 5px;
}
.container {
    display: flex;
    flex-direction: row;
    margin: 20px 0px;
}

.specs {
  width: 30%;
}

.image {
  width: 70%;
}


.back-drop { 
  width: 100%;
  height: 100%;
}

.description {
  text-align: center;
  margin: 3px auto;
  padding: 20px;
}

.title {
  font-size: 35px;
  text-align: center;
  margin: 3px;
}

.rating {
  font-size: 15px;
  text-align: center;
  margin: 5px;
}

.btn {
  background-color: black;
  border-color: red;
  color: red;
  font-size: 1rem;
  height: 6%;
  width: 50%;
  margin: 10px 110px;
}

.back-btn {
  background-color: black;
  border-color: red;
  color: red;
  font-size: 1rem;
  height: 6%;
  width: 50%;
  margin: 10px 110px;
}

.btn:hover, .back-btn:hover {
  background-color: red;
  border-color: black;
  color: black;
}

.date {
  font-size: 15px;
  text-align: center;
  margin: 5px;
}





.sign-in-form{
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: auto;
}

.sign-up{
  background-color: black;
  border-color: red;
  color: red;
  height: 30px;
  margin: 10px;
  width: 175px;
}

.sign-up-btn {
  background-color: black;
  border-color: red;
  border-radius: 20px;
  color: red;
  font-size: 1.5rem;
  height: 35px;
  margin: 10px;
  width: 150px;
}

::-webkit-input-placeholder {
  color: red;
  font-size: 15px;
}

::-moz-placeholder {
  color: red;
  font-size: 15px;
}

:-ms-input-placeholder {
  color: red;
  font-size: 15px;
}

::-ms-input-placeholder {
  color: red;
  font-size: 15px;
}

::placeholder {
  color: red;
  font-size: 15px;
}

.sign-up-bar {
  background-color: red;
}


.header {
    display: grid;
    grid-template-columns: 1fr 1fr .5fr .5fr;
    grid-template-rows: 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
}

.form {
  grid-area: 1 / 1 / 2 / 2;
}
.nav-fav {
  grid-area: 1 / 3 / 2 / 4; 
  margin: auto 0 ;
}

.nav-sign-up {
  grid-area: 1 / 4 / 2 / 5;
  margin: auto 0;
}

.nav-log-in {
  grid-area: 1 / 0 / 2 / 5;
  margin: auto 20px;
}

.nav-btn {
  background-color: black;
  border-color: red;
  color: red;
  font-size: 1.5rem;
  width: 90%;
}

.nav-log-in-btn {
  background-color: black;
  border-color: red;
  color: red;
  font-size: 1.5rem;
  width: 50%;
}

.nav-log-in-btn:hover {
  background-color: red;
  border-color: black;
  color: black;
}

.nav-btn:hover {
  background-color: red;
  border-color: black;
  color: black;
}

.app-name {
  background-color: black;
  border: none;
  color:red;
  font-size: 6rem;
  letter-spacing: 3px;
  font-weight: 700;
}

.app {
  grid-area: 1 / 2 / 2 / 3;
  justify-content: center;
  display: flex;
  text-decoration: none;
}

.bang {
  color: white;
  font-size: 6rem;
  padding-top: 14px;
}

.logged-in-bar {
  display: flex;
  flex-direction: row;
  justify-content: center;
  background-color: red;
}

.user-name {
  color: black;
  margin: 15px 10px;
}

.sign-out-btn {
  background-color: black;
  border-color: red;
  border-radius: 20px;
  color: red;
  font-size: 1.5rem;
  height: 35px;
  margin: 10px;
  width: 150px;
}

